@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500&display=swap");
*, ::after, *::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-family: "Inter", sans-serif;
  background-color: #e5ecf4;
  line-height: 1.6;
  user-select: none;
}

main {
  margin: 1rem;
}

.mx-auto {
  margin-left: auto;
  margin-right: auto;
}

.mb-0 {
  margin-bottom: 0;
}

.mb-1 {
  margin-bottom: 1rem;
}

.mb-2 {
  margin-bottom: 2rem;
}

.mb-3 {
  margin-bottom: 3rem;
}

.mb-4 {
  margin-bottom: 4rem;
}

.mb-5 {
  margin-bottom: 5rem;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style-type: none;
}

.logo {
  display: block;
  width: 150px;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.avatar {
  width: 150px;
  height: 150px;
  object-fit: cover;
  border-radius: 50%;
}

.illustration {
  width: 400px;
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.card {
  position: relative;
  overflow: hidden;
  height: fit-content;
  padding: 2rem;
  background-color: #f8f8f8;
  box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.15);
  border-radius: 15px;
  text-align: center;
}
.card-title {
  font-size: clamp(0.875rem, 4vw, 1.75rem);
  font-weight: 500;
  color: #29335c;
}
.card-title.success {
  margin-top: 1.5rem;
  color: #559e32;
}
.card-title.success::before {
  content: "âœ“";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: block;
  width: 40px;
  height: 40px;
  margin: 1rem auto;
  font-size: 1.55rem;
  background-color: #559e32;
  color: #f8f8f8;
  border-radius: 50%;
}
.card-title.failed {
  margin-top: 1.5rem;
  color: #d33329;
}
.card-title.failed::before {
  content: "X";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  display: block;
  width: 40px;
  height: 40px;
  margin: 1rem auto;
  font-size: 1.55rem;
  background-color: #d33329;
  color: #f8f8f8;
  border-radius: 50%;
}
.card-subtitle {
  font-size: clamp(0.75rem, 4vw, 1.125rem);
  font-weight: 400;
  color: #66717e;
}
.card-info {
  font-size: clamp(0.688rem, 4vw, 0.938rem);
  font-weight: 400;
  color: #66717e;
}
.card-info span {
  color: #726bea;
}

.progress-bar {
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  counter-reset: step;
}
.progress-bar li {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
  font-size: 0.889rem;
  font-weight: 400;
  color: #66717e;
}
.progress-bar li::before {
  content: counter(step);
  counter-increment: step;
  margin-right: 0.3rem;
  padding: 0 0.5em;
  background-color: #66717e;
  color: #f8f8f8;
  border-radius: 5px;
}
.progress-bar li:not(:first-child)::before {
  margin-left: 0.3rem;
}
.progress-bar li:not(:last-child):after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 1.5px;
  background-color: #66717e;
}
.progress-bar li.active {
  color: #726bea;
}
.progress-bar li.active::before {
  background-color: #726bea;
}

.input-group {
  position: relative;
  margin-bottom: 1.5rem;
  text-align: start;
}

.form-control {
  width: 100%;
  padding: 0.8em 1em;
  font-size: clamp(0.75rem, 4vw, 1rem);
  background: none;
  outline: none;
  color: #29335c;
  caret-color: #726bea;
  border: 1px solid #29335c;
  border-radius: 5px;
}
.form-control:focus {
  border-color: #726bea;
}
.form-control.invalid {
  margin-bottom: 0.2rem;
  border-color: #d33329;
}

.form-label {
  position: absolute;
  left: 0;
  padding: 0.6em 1em;
  font-size: clamp(0.75rem, 4vw, 1rem);
  color: #66717e;
  pointer-events: none;
  transition: 150ms cubic-bezier(0.4, 0, 0.2, 1);
}

.form-control:focus ~ .form-label {
  padding: 0 0.5em;
  background-color: #f8f8f8;
  color: #726bea;
  transform: translateY(-50%) scale(0.8);
}

.form-control:not(:placeholder-shown).form-control:not(:focus) ~ .form-label {
  padding: 0 0.5em;
  background-color: #f8f8f8;
  color: #29335c;
  transform: translateY(-50%) scale(0.8);
}

.invalid-feedback {
  font-size: 0.8rem;
  color: #d33329;
}

.btn {
  font: inherit;
  background: none;
  color: inherit;
  border: none;
  outline: none;
}
.btn-primary {
  width: 100%;
  padding: 0.5em 1em;
  font-size: clamp(0.75rem, 4vw, 1rem);
  font-weight: 400;
  background-color: #726bea;
  color: #f5f5f5;
  border-radius: 5px;
  cursor: pointer;
}
.btn-outline-primary {
  width: 100%;
  padding: 0.5em 1em;
  font-size: clamp(0.75rem, 4vw, 1rem);
  font-weight: 400;
  border: 1px solid #726bea;
  color: #726bea;
  border-radius: 5px;
  cursor: pointer;
}